<template>
  <span class="demo-input-label">待上新岗位</span>
  <el-select v-model="value" class="m-2" placeholder="Select" size="large" @change="query()" >
      <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
      />
    </el-select>
  <div class="show">
    <span class="demo-input-label">公司岗位信息</span>
    <el-table :data="tableData" stripe max-height="500" style="width: 100%" border>
      <el-table-column type="expand">
        <template #default="props">
          <div>
            <p>{{ props.row.desc }}</p>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="jdId" label="jdId" width="100" />
      <el-table-column prop="title" label="岗位" width="180" />
      <el-table-column prop="jobType" label="岗位属性" width="180" />
      <el-table-column prop="type" label="招聘类别" width="180" />
      <el-table-column prop="price" label="薪资" width="180" />
      <el-table-column prop="city" label="城市" width="180" />
      <el-table-column prop="desc" :show-overflow-tooltip="true" label="岗位描述" width="180" />
      <el-table-column prop="email" label="邮箱" width="180" />
      <el-table-column prop="circuit" label="行业" width="180" />
      <el-table-column prop="date" label="日期" width="110" />
    </el-table>
  </div>
  <label class="lab">
    <span class="demo-input-label">公司</span>
    <el-input v-model="company" placeholder="Please input" clearable />
  </label>
  <label class="lab">
    <span class="demo-input-label">岗位</span>
    <el-input v-model="jd" placeholder="Please input" clearable />
  </label>
  <br />
  <div id="btn">
    <el-popconfirm title="确认信息无误后，确认发送?" @confirm="send">
      <template #reference>
        <el-button type="success">确认并推送</el-button>
      </template>
    </el-popconfirm>
  </div>
</template>

<script>
import {onMounted, ref} from "vue"
import axios from "axios";
import qs from "qs";
import { ElNotification } from "element-plus";
import {useRouter} from "vue-router"
import {getApiRootPath} from "@/utils/url"
const baseUrl = getApiRootPath()

export default {
  setup() {
    const jobTypes = [
     "技术类",
    "设计类",
    "财务类",
    "产品/运营类",
    "战略/市场类",
    "行政类",
    "法务类",
    "销售类",
    "其他",
]
    const value = ref('')
    const options = ref([])
    const tableData = ref([])
    onMounted(()=>{
      // jd.value = '取岗位属性（产品、研发这种）'
      // company.value = 'XX公司名'
      axios.get(`${baseUrl}/api/push/get-recent-company-cname`)
          .then((res)=>{
            const {status,data,message} =  res.data
            console.log(data)
            if (status === '0') {
              if ( data.length > 0 ) {
                options.value = data.reduce((arr,item)=>{
                  const {cName} = item
                   arr.push({value:cName,label:cName})
                    return arr
                  },[])
              } else {
                ElNotification({
                  title: "暂无待确认的岗位信息",
                  message: message,
                  type: "success",
                });
              }
            } else {
              ElNotification({
                title: "查询失败",
                message: "query fail",
                type: "warning",
              });
            }
          })
    })
    const query = () => {
      axios({
        method: "post",
        url: `${baseUrl}/api/push/get-recent-jd`,
        data: qs.stringify({ cName: value.value }),
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then((res)=>{
        const {status,data,message} =  res.data
        console.log(data)
        if (status === '0') {
              if ( data.length > 0 ) {
                tableData.value = data
                console.log(data)
                company.value = value.value
                const types = []
                data.forEach((item)=>{
                  const idx =  types.indexOf(item.jobType)
                  const isValidJobType = jobTypes.indexOf(item.jobType)
                  if ( idx === -1 && isValidJobType!==-1) {
                    types.push(item.jobType)
                  }
                })
                console.log("types:"+types)
                jd.value = types.join("+")
              } else {
                ElNotification({
                  title: "暂无待确认的岗位信息",
                  message: message,
                  type: "success",
                });
              }
            } else {
              ElNotification({
                title: "查询失败",
                message: "query fail",
                type: "warning",
              });
            }
          })
    }
    let company = ref(); // 绑定 公司
    let jd = ref(); // 绑定 jd
    const router = useRouter();
    const send = () => {
      console.log("send")
      if (!company.value || !jd.value) {
        // alert("输入不能为空");
        ElNotification({
          title: "Warning",
          message: "输入不能为空!",
          type: "warning",
        });
        return;
      }

      axios({
        method: "post",
        url: `${baseUrl}/sendJDUpdate`,
        data: qs.stringify({ company: company.value, jd: jd.value }),
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then((res) => {
        if (res.data.status === "0") {
          ElNotification({
            title: "发送成功",
            message: "Send successfully",
            type: "success",
          });
          window.location.reload()
        } else if (res.data.status === '403') {
          ElNotification({
            title: "无发送权限",
            message: res.data.message,
            type: "error",
          });
        } else {
          ElNotification({
            title: "验证过期",
            message: res.data.message,
            type: "error",
          });
          router.push("/home/login");
        }
      });
    };
    return {
      value,
      options,
      company,
      jd,
      tableData,
      send,
      query
    };
  },
};
</script>

<style>
.lab {
  display: block;
  width: 200px;
  height: 7vh;
  margin: 2vh;
}

#btn {
  display: flex;
  justify-content: center;
}

button {
  /*width: 15.2vw;*/
  width: 200px;
  height: 4.2vh;
  font-size: 2.8vh;
}

