import {
    createRouter,
    createWebHashHistory
    // createWebHistory
} from 'vue-router'
import JdVerify from "../components/JdVerify.vue"
import CompanyVerify from "../components/CompanyVerify.vue"
const Home = () => import('../components/Home.vue')
const Login = () => import('../components/Login.vue')
const Content = () => import('../components/content.vue')
const Visualization = () => import('../components/Visualization.vue')
const VisualizationType = () => import('../components/VisualizationType.vue')
const VisualizationTitle = () => import('../components/VisualizationTitle.vue')
const Data = () => import('../components/Data.vue')

const routes = [{
        path: '/',
        redirect: '/content/visualization'
    },
    {
        path: '/content',
        component: Content,
        redirect: '/content/companyUpdate',
        meta: {
            auth: true
        },
        children: [
            {
                path: "companyUpdate",
                component: CompanyVerify
            },
            {
                path: "jdUpdate",
                // component: JdUpdate
                component: JdVerify
            },
            {
                path: "visualization",
                component: Visualization
            },
            {
                path: "visualizationType",
                component: VisualizationType
            },
            {
                path: "visualizationTitle",
                component: VisualizationTitle
            },
            {
                path: "data",
                component: Data
            },
        ]
    },
    {
        path: '/home',
        component: Home,
        children: [{
                path: '/home',
                redirect: 'home/login'
            },
            {
                path: 'login',
                component: Login
            }
        ]
    }
]

const router = createRouter({
    history: createWebHashHistory(),
    routes: routes
})

router.beforeEach((to, from, next) => {
    if (to.meta.auth) {
        let token = localStorage.getItem("token");
        if (token) {
            next();
        } else {
            next({
                path: '/home/login',
            })
        }
    } else {
        next();
    }
})

export default router;
